.Chat{
    display: flex;
    flex-direction: column;
    flex: 0.65;
}
.chat__header{
    display: flex;
    padding: 20px;
    align-items: center;
    border-bottom: 1px solid lightgray;
    height: 10%;
}
.chat__header > h3{
    margin-bottom: 3px;
    font-weight: 500;
}
.chatheader_info{
    flex: 1;
    padding-left: 20px;
    
}
.chatheader_info > p{
    color: gray;
}
.chat_body{
    flex: 1;
    /* background-image: "https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png"; */
    /* background-repeat: repeat; */
    /* background-position: center; */
    background-color: #ECE5DD;
    padding: 30px;
    overflow-y: scroll;
}
.chat_message{
    display: flex;
    /* flex-direction: column; */
    background-color: #ffff;
    border-radius: 10px;
    padding: 10px;
    font-size: 16px;
    position: relative;
    width: fit-content;
    margin-bottom: 30px;
    display: block;
    
}
.chat_timestamp{
    color: grey;
    font-size: xx-small;
    bottom: 15px;
    margin-left: 5px;
}
.chat_name{
    position: absolute;
    top: -19px;
    font-weight: 700;
    font-size: small;
}
.chat_reciever{
    margin-left: auto;
    background-color: #dcf8c6;
}
.chat_footer{
    display: flex;
    padding: 10px;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid lightgray;
}
.chat_footer > form{
    flex: 1;
    display: flex;
}
.chat_footer > form > input{
    flex: 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
    margin-right: 5px;
    outline: none;
    border-width: 0;
}
.chat_footer > .MuiSvgIcon-root{
    margin-right: 5px;
     color: gray;
}
.chat_footer > .send{
    display: none;
}