.Sidebar{
    display: flex;
    flex-direction: column;
    flex: 0.35;
    border-right: 1px solid lightgray;
}
.sidebar_header{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgray;
}
.sidebar_header_right{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}
.sidebar_header_right > .MuiSvgIcon-root{
    margin-right: 2vw;
    font-size: 24px !important;
}
.sidebar__search{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
    
}
.sidebar__searchContainer{
    display: flex;
    align-items: center;
    
    background-color: white;
    width: 100%;
    border-radius: 20px;
    height: 35px;
}
.sidebar__searchContainer > .MuiSvgIcon-root{
    color: gray;
    padding: 10px;
}
.sidebar__searchContainer > input{
    outline: none;
    border-width: 0;
    margin-left: 10px;
}
.sidebar__chats{
    flex: 1;
    background-color: white;
    overflow-y: scroll;
}
