body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Sidebar{
    display: flex;
    flex-direction: column;
    flex: 0.35 1;
    border-right: 1px solid lightgray;
}
.sidebar_header{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgray;
}
.sidebar_header_right{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}
.sidebar_header_right > .MuiSvgIcon-root{
    margin-right: 2vw;
    font-size: 24px !important;
}
.sidebar__search{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
    
}
.sidebar__searchContainer{
    display: flex;
    align-items: center;
    
    background-color: white;
    width: 100%;
    border-radius: 20px;
    height: 35px;
}
.sidebar__searchContainer > .MuiSvgIcon-root{
    color: gray;
    padding: 10px;
}
.sidebar__searchContainer > input{
    outline: none;
    border-width: 0;
    margin-left: 10px;
}
.sidebar__chats{
    flex: 1 1;
    background-color: white;
    overflow-y: scroll;
}

.sidebarChat{
    display: flex;
    /* align-items: center; */
    padding: 10px;
    cursor: pointer;
    border-bottom: 1px solid #f6f6f6;
}
.sidebarChat:hover{
    background-color: #ebebeb;
}   
.sidebarChat__info > h2{
    font-size: 15px;
    margin-bottom: 8px; 
    
}
.sidebarChat__info{
    margin-left: 15px;
    margin-top: -15px;
}

.Chat{
    display: flex;
    flex-direction: column;
    flex: 0.65 1;
}
.chat__header{
    display: flex;
    padding: 20px;
    align-items: center;
    border-bottom: 1px solid lightgray;
    height: 10%;
}
.chat__header > h3{
    margin-bottom: 3px;
    font-weight: 500;
}
.chatheader_info{
    flex: 1 1;
    padding-left: 20px;
    
}
.chatheader_info > p{
    color: gray;
}
.chat_body{
    flex: 1 1;
    /* background-image: "https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png"; */
    /* background-repeat: repeat; */
    /* background-position: center; */
    background-color: #ECE5DD;
    padding: 30px;
    overflow-y: scroll;
}
.chat_message{
    display: flex;
    /* flex-direction: column; */
    background-color: #ffff;
    border-radius: 10px;
    padding: 10px;
    font-size: 16px;
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-bottom: 30px;
    display: block;
    
}
.chat_timestamp{
    color: grey;
    font-size: xx-small;
    bottom: 15px;
    margin-left: 5px;
}
.chat_name{
    position: absolute;
    top: -19px;
    font-weight: 700;
    font-size: small;
}
.chat_reciever{
    margin-left: auto;
    background-color: #dcf8c6;
}
.chat_footer{
    display: flex;
    padding: 10px;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid lightgray;
}
.chat_footer > form{
    flex: 1 1;
    display: flex;
}
.chat_footer > form > input{
    flex: 1 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
    margin-right: 5px;
    outline: none;
    border-width: 0;
}
.chat_footer > .MuiSvgIcon-root{
    margin-right: 5px;
     color: gray;
}
.chat_footer > .send{
    display: none;
}
.App{
    display:grid;
    place-items:center;
    height: 100vh;
    background-color: #dadbd3;
}
.app-body{
    display: flex;
    background-color: #ededed;
    margin-top: 18px;
    height: 90vh;
    width: 90vw;
    box-shadow: -1px 4px 20px -6px rgba(0,0,0,0.75);
}
